.container{
  grid-gap: 20px;
  grid-template-columns: 1fr 1.5fr;
  display: grid;
  padding-bottom: 10%;
  align-items: center;
  justify-items: start;
}

.inicio{
  background: #ffffff9c;
  background-position: 100% 100%;
  background-repeat: no-repeat;
  background-size: 50%;
  border-radius: 24px;
  box-shadow: -1px -4px 5px -1px rgba(13,13,13,0.55);
-webkit-box-shadow: -1px -4px 5px -1px rgba(13,13,13,0.55);
-moz-box-shadow: -1px -4px 5px -1px rgba(13,13,13,0.55);
  box-sizing: content-box;
  height: 360px;
  margin-left: 13%;
  margin-top: 18%;
  padding-left: 36px;
  padding-right: 35px;
  padding-top: 43px;
  display: flex;
  align-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;

}

.fin{
  margin: 60px 90px 10px 13px;
    justify-self: center;
    /* padding: 43px 23px 7px 53px; */
    font-family: 'Poppins', sans-serif;
    width: 58%;
    background: #ffffff9c;
    border-radius: 24px;
    box-shadow: -1px -4px 5px -1px rgba(13,13,13,0.55);
    -webkit-box-shadow: -1px -4px 5px -1px rgba(13,13,13,0.55);
    -moz-box-shadow: -1px -4px 5px -1px rgba(13,13,13,0.55);
    /* box-sizing: content-box; */
    height: 89%;
    margin-left: 6%;
    margin-top: 18%;
    padding-left: 36px;
    padding-right: 35px;
    /* padding-top: 43px; */
    display: flex;
    align-content: center;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: center;
  
}

.title-task{
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 250%;
  margin: 0;
}
.title-principal-task{
  font-family: 'Poppins', sans-serif;
  display: block;
  font-size: 150%;
  margin-block-start: 0.83em;
  margin-block-end: 0.23em;
  margin-inline-end: 0;
  margin-inline-start: 0;
  font-weight: 700;
  text-align: start;
  color: #dd8d20;
}
.title-secondary-task{
  font-family: 'Poppins', sans-serif;
  font-size: small;
  font-weight: 400;
  color: #dd8d20;
  margin: 0;
  padding-bottom: 22px;
}
.create-todo{
  color: #070707;
  font-family: 'Poppins', sans-serif;
  font-size: 85%;
  border-radius: 7px;
  width: 100%;
  height: 30px;
  margin: 0 0 21px 0;
}

.new-task-button{
  font-family: 'Poppins', sans-serif;
  display: flex;
  justify-content: space-between;
  width: 76%;
}

.buttons-creaters{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: initial;
  flex-direction: row;
}


.primary-button{
  cursor: pointer;
  font-family: 'Poppins', sans-serif;
  border-radius: 6px;
  border-color: rgba(240, 248, 255, 0);
  width: 100px;
  height: 31px;
  background-color: #dd8d20;
  color:white;
  font-size: 75%;
}
.primary-button:hover{
  color: black;
}
.secondary-button{
  cursor: pointer;
  font-size: 75%;
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  border-radius: 6px;
  border-color: rgba(240, 248, 255, 0);
  width: 80px;
  height: 31px;
  background-color: rgb(220, 224, 227);
  color: rgb(69, 69, 69)
}
.secondary-button:hover{
  color: white;
}

.task-for-complete{
  font-size: 300;
  margin: 0;
  padding: 0 0;
  list-style: none;
  
}

.search{
  color: #057777;
  font-family: 'Poppins', sans-serif;
  font-size: 84%;
  border-radius: 9px;
  width: 100%;
  height: 30px;
  border-color: #e8f3f3;
  margin: 10px 0 13px 0;
}

.todo-item{
  background-color:#FFECDD;
  position:relative;
  display:flex;
  justify-content:center;
  align-items:center;
  margin-top:24px;
  box-shadow:0 5px 50px rgba(32,35,41,.15);
  height: 56px;
  border-radius: 21px;
}

.todo-item-p{
  margin:24px 0 24px 24px;
  width:calc(100% - 120px);
  font-size:18px;
  line-height:24px;
  font-weight:400
}

.todoItem-p-completed{
  text-decoration: line-through;

}

.icon{
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 43px;
  width: 37px;
  font-size: 24px;
  font-weight: bold;
}


.icon-check{
  position: absolute;
  left: 12px;
}

.icon-delete{
  position: absolute;
  top: -16px;
  right: -1px;
}

.icon-delete:hover .icon-svg{
  fill: red;
}

.icon-check:hover .icon-svg{
  fill: green;
}


.proggres-linear{
  display: flex;
  justify-self: center;
  margin: 29px 10px 15px 10px;
  border-radius: 15px;
}
